<template>
  <div id="slider-home">
    <hooper :settings="hooperSettings" style="height: auto">
      <slide v-for="item in items" :key="items.id" class="item-slide-home">
        <a v-if="item.link != ''" v-bind:href="item.link">
          <img v-bind:src="item.src" v-bind:alt="item.name"/>
        </a>
        <a v-else href="javascript:void(0)">
          <img v-bind:src="item.src" v-bind:alt="item.name"/>
        </a>
      </slide>
      <hooper-pagination slot="hooper-addons"></hooper-pagination>
      <hooper-navigation slot="hooper-addons"></hooper-navigation>
    </hooper>
  </div>
</template>
<script>
  import { Hooper, Slide, Navigation as HooperNavigation, Pagination as HooperPagination } from 'hooper';
  import 'hooper/dist/hooper.css';
  export default {
      components: { 
        Hooper,
        Slide,
        HooperNavigation,
        HooperPagination
      },
      data () {
        return {
          items: [],
          hooperSettings: {
            itemsToShow: 1,
            infiniteScroll: true,
            autoPlay: true,
            playSpeed: 2500,
            wheelControl: false,
            keysControl: false,
          }
        }
      },
      mounted() {
        this.getSlider();
      },
      methods: {
        getSlider() {
          axios.get('api/get-slider-home')
            .then((response) => {
              this.items = response.data;
            })
        }
      }
    }
</script>