<template>
    <div class="form-search input-group search-relative">
        <input type="text" class="form-control query_string" name="query_string" v-model="searchText" @keyup="search()" placeholder="Sn phm bn cn tm?">
        <button type="submit" id="tbl_search_header" title="Tm kim" class="icon-fallback-text"><i class="fas fa-search"></i> Tm kim</button>
        <div class="result-autocomplete">
            <ul class="list-item-result" v-html="searchArrs">
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                searchText: '',
                searchArrs: '',
            }
        },
        methods: {
            search() {
                if(this.searchText.length >= 5){
                    axios.post('api/search-with-vue', {
                        name: this.searchText
                    })
                    .then((response) => {
                        this.searchArrs = response.data;
                    });
                }
            }
        }
    }
</script>